<template>
  <div>
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane v-for="(item, index) in filtersDataList" :key="index" :label="item.sysName" :name="item.sysId"></el-tab-pane>
    </el-tabs>
    <div class="float-right">
      <el-checkbox v-model="checked" @change="handleCheck">关联子节点</el-checkbox>
      <el-button type="primary" size="small" @click="getCurrentKey">保存</el-button>
    </div>
    <div class="clearfix"></div>
    <el-scrollbar style='height: 400px;' v-loading="treeLoading">
      <org-tree ref="tree" :treeData="treeData" :checkbox="treeCheckBox" :checkStrictly="checked" :defaultChecked="defaultChecked" @check="check"></org-tree>
    </el-scrollbar>
  </div>
</template>

<script>
import orgTree from "@/components/baseComponents/orgTree.vue";
export default {
  name: 'allot-area-dialog',
  props: [
    'checkedData'
  ],
  components: {
    orgTree
  },
  computed: {
    filtersDataList () {
      return this.dataList.filter(item => item.sysStsatus === '1')
    }
  },
  data () {
    return {
      roleId: '',
      checked: false,
      treeData: [],
      treeCheckBox: true,//tree复选框是否有
      defaultChecked: [],
      dataList: [],//机构数据
      sysId: '',//机构id
      activeName: this.$store.state.common.areaSysId,
      treeIds: [],
      treeLoading: true
    }
  },
  watch: {},
  created () {
    this.sysDataList();
    var id = this.$store.state.common.areaSysId;
    this.getData(id);
  },
  methods: {
    // 当前点击的对象
    handleCheck (bol) {
      this.treeIds = [];
      const newArr = this.setAllIsCheck(this.treeData, bol)
      this.treeData = newArr;
      if (bol) {
        this.defaultChecked = this.getIsCheckCurrent(newArr);
        return
      }
      this.$refs.tree.clearCheckedKeys();
    },
    // 获取当前点击的checkbox的对象
    check (len) {
      this.checked = (len === this.treeIds.length ? true : false)
    },
    sysDataList () {
      this.$http({
        url: this.$http.adornUrl(this.API.getAllApplySystemInfoSystem),
        method: 'post',
        data: this.$http.adornData({
          searchCount: false, //分页默认true，不需要传false
        })
      }).then((res) => {
        var resData = res.data;
        if (resData.code === 0) {
          this.dataList = resData.data;
        } else {
          this.$message.error(resData.msg);
          this.dataList = [];
        }
      })
    },
    handleClick (tab) {
      this.sysId = tab.name;
      this.getData(this.sysId)
    },
    getData (id) {
      this.treeIds = [];
      this.$http({
        url: this.$http.adornUrl(this.API.getMenuTreetBysIdMenu),
        method: 'post',
        data: this.$http.adornData({
          sysId: id,
          areaId: this.checkedData.areaId,
        })
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.treeData = data.data;
          this.defaultChecked = this.getIsCheckCurrent(data.data);
          this.treeLoading = false;
        } else {
          this.$message.error(data.msg);
          this.treeData = [];
          this.defaultChecked = [];
          this.treeLoading = false;
        }
      });
    },
    getCurrentKey () { //保存选中节点
      // console.log(this.$refs.tree.getCheckedKeys().concat(this.$refs.tree.getHalfCheckedKeys()).join(','))
      this.$http({
        url: this.$http.adornUrl(this.API.batchInsertSysAreaMenuArea),
        method: 'post',
        data: this.$http.adornData({
          areaId: this.checkedData.areaId, //区域id
          sysId: this.sysId || this.$store.state.common.areaSysId, //系统id
          menuId: this.$refs.tree.getCheckedKeys().join(','),//菜单id
        })
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message.success('操作成功');
        } else {
          this.$message.error(data.msg);
        }
      });
    },
    getIsCheckCurrent (arr) { //多层树结构中取出是否选中
      const setCheckedKeys = [];
      if (!arr) return;
      const getChild = (arr) => {
        arr.forEach(item => {
          this.treeIds.push(item.id)
          if (item.isCheck) setCheckedKeys.push(item.id);
          if (item['children'] && item['children'].length > 0) {
            getChild(item.children)
          }
        })
        this.checked = (setCheckedKeys.length === this.treeIds.length ? true : false)
        return setCheckedKeys;
      }
      return getChild(arr)
    },
    setAllIsCheck (arr, bol) {
      const getChild = (arr) => {
        arr.forEach(item => {
          item.isCheck = bol
          if (item['children'] && item['children'].length > 0) {
            getChild(item.children)
          }
        })
        return arr
      }
      return getChild(arr)
    }
  }
}
</script>

<style lang="scss" scoped>
.float-right {
  padding-bottom: 10px;
  margin-top: 10px;
  .el-checkbox {
    padding-right: 20px;
  }
}
</style>



